.App {
  text-align: center;
  background-image: url("../src/chess_background.jpg");
  background-size: 1500px 1000px;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.body-left {
  height: 100vh;
  width: 750px;
}

.body-right {
  height: 100vh;
  width: 750px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.intro {
  width: 500px;
  height: 60px;
  color: white;
  font-size: 35px;
  font-family: 'Permanent Marker', cursive;
  cursor: pointer;
}

.intro-icons {
  width: 500px;
  height: 60px;
  color: white;
  font-size: 35px;
  font-family: 'Permanent Marker', cursive;
  cursor: pointer;
}

.intro-title {
  width: 500px;
  height: 40px;
  color: white;
  font-size: 20px;
  font-family: 'Permanent Marker', cursive;
}

.intro-about {
  width: 500px;
  height: 40px;
  color: white;
  font-size: 20px;
  font-family: 'Helvetica Neue';
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.icon-gh, .icon-li {
  font-size: 35px;
  color: white;
}

.icon-gh:hover {
  color: black;
}

.icon-li:hover {
  color: #0077B5;
}


.show.show {
  opacity: 1;
  visibility: visible;
}

@media only screen and (max-width: 480px) {
  .App {
    background-image: url("../src/wood.jpg");
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
  }
}



